.head{
  margin-top: 1%;
  text-align: center;
  background-color: #1390CF;
  border-radius: 30px;
  margin-left: 40px;
  margin-right: 40px;
}
.image{
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.i{
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1%;
  margin-bottom: 1%;
}
input[type=file] {
  background-color: #cce9f8;
  border: none;
  color: black;
  padding: 16px 32px;
  text-decoration: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2%;
  margin-bottom: 1%;

}
button {
  background-color:#72bde2;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  width: 20%;
